import React, { Component } from 'react'
import PropTypes from 'prop-types';
import classNames from 'classnames';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import ErrorIcon from '@material-ui/icons/Error';
import InfoIcon from '@material-ui/icons/Info';
import CloseIcon from '@material-ui/icons/Close';
import green from '@material-ui/core/colors/green';
import amber from '@material-ui/core/colors/amber';
import IconButton from '@material-ui/core/IconButton';
import Snackbar from '@material-ui/core/Snackbar';
import SnackbarContent from '@material-ui/core/SnackbarContent';
import WarningIcon from '@material-ui/icons/Warning';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux';
import { empty } from '../../is-empty';
import { CloseNotification } from '../../actions/authentication';

const variantIcon = {
    success: CheckCircleIcon,
    warning: WarningIcon,
    error: ErrorIcon,
    info: InfoIcon,
  };
  
  const styles1 = theme => ({
    success: {
      backgroundColor: green[600],
    },
    error: {
      backgroundColor: theme.palette.error.dark,
    },
    info: {
      backgroundColor: theme.palette.primary.dark,
    },
    warning: {
      backgroundColor: amber[700],
    },
    icon: {
      fontSize: 20,
    },
    iconVariant: {
      opacity: 0.9,
      marginRight: theme.spacing.unit,
    },
    message: {
      display: 'flex',
      alignItems: 'center',
    },
  });

  function MySnackbarContent(props) {
    const { classes, className, message, onClose, variant, ...other } = props;
    const Icon = variantIcon[variant];
  
    return (
      <SnackbarContent
        className={classNames(classes[variant], className)}
        aria-describedby="client-snackbar"
        message={
          <span id="client-snackbar" className={classes.message}>
            <Icon className={classNames(classes.icon, classes.iconVariant)} />
            {message}
          </span>
        }
        action={[
          <IconButton
            key="close"
            aria-label="Close"
            color="inherit"
            className={classes.close}
            onClick={onClose}
          >
            <CloseIcon className={classes.icon} />
          </IconButton>,
        ]}
        {...other}
      />
    );
  }

  MySnackbarContent.propTypes = {
    classes: PropTypes.object.isRequired,
    className: PropTypes.string,
    message: PropTypes.node,
    onClose: PropTypes.func,
    variant: PropTypes.oneOf(['success', 'warning', 'error', 'info']).isRequired,
  };

  const MySnackbarContentWrapper = withStyles(styles1)(MySnackbarContent);
    
  const styles2 = theme => ({
    margin: {
      margin: theme.spacing.unit,
    },
  });

 class Notification extends Component {
     constructor(){
        super();
        this.state = {
            open: false,
            variant_type: "success",
            message: ""
        }
     }
     
     componentDidMount() {
      
     }

     static getDerivedStateFromProps(nextProps, prevState){
        if(nextProps.Notification !== prevState.Notification){
            if(!empty(nextProps.Notification.Notification_Error)){
                return { open: true, variant_type: "error", message: nextProps.Notification.Notification_Error.message }
            }else if(!empty(nextProps.Notification.Notification_Success)){
                return { open: true, variant_type: "success", message: nextProps.Notification.Notification_Success.message }
            }else if(!empty(nextProps.Notification.Notification_Warning)){
                return { open: true, variant_type: "warning", message: nextProps.Notification.Notification_Warning.message }
            }else if(!empty(nextProps.Notification.Notification_Info)){
                return { open: true, variant_type: "info", message: nextProps.Notification.Notification_Info.message }
            }
            else{
            return { open: false };
            }
        }else{
            return null;
        }
    }

    componentDidUpdate(prevProps, prevState){
        if(prevProps.Notification !== this.props.Notification){
            if(!empty(this.props.Notification.Notification_Error)){
                this.setState({
                    open: true,
                    variant_type: "error",
                    message: this.props.Notification.Notification_Error.message
                })
            }else if(!empty(this.props.Notification.Notification_Success)){
                this.setState({
                    open: true,
                    variant_type: "success",
                    message: this.props.Notification.Notification_Success.message
                })
            }else if(!empty(this.props.Notification.Notification_Warning)){
                this.setState({
                    open: true,
                    variant_type: "warning",
                    message: this.props.Notification.Notification_Warning.message
                })
            }else if(!empty(this.props.Notification.Notification_Info)){
                this.setState({
                    open: true,
                    variant_type: "info",
                    message: this.props.Notification.Notification_Info.message
                })
            }
            else{
                return {open: false}
            }
        }else{
            return null
        }
    }

     handleClose = (event, reason) => {
        if (reason === 'clickaway') {
          return;
        }
        this.props.CloseNotification(this.state.variant_type);
        this.setState({ open: false });
      };

  render() {
    const { variant_type, open, message } = this.state;

    return (
      <div>
          <Snackbar
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          open={open}
          autoHideDuration={6000}
          onClose={this.handleClose}
        >
          <MySnackbarContentWrapper
            onClose={this.handleClose}
            variant={variant_type}
            message={message}
          />
        </Snackbar>
      </div>
    )
  }
}

Notification.propTypes = {
    classes: PropTypes.object.isRequired,
    Notification: PropTypes.object.isRequired,
    CloseNotification: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
    Notification: state.Notification
})
  
export default connect(mapStateToProps, {CloseNotification})(withStyles(styles2)(Notification))